<script>

import axios from "axios";
import {baseUrl} from "../../../config";

export default {
  components: {

  },
  data() {
    return{
      tests_id:'',
      regions:[],
      regions_id:'',
      districts:[],
      districts_id:'',
      organizations:[],
      organizations_id:'',
      statsExel:[],
      tests:[],
      test_result:[],
      main_or:[],
      loadings:true
    }
  },
  methods: {
    getDistrct() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/integration/districts/?region_id=${this.$route.params.reg_id}`)
        .then(res => {
          this.districts = res.data
        })
      this.getOr()
    },
    getOr() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/integration/organizations/?region_id=${this.regions_id}`)
        .then(res => {
          this.organizations = res.data
        })
    },
    getOrDis() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/integration/organizations/?region_id=district_id=${this.regions_id}&district_id=${this.districts_id}`)
        .then(res => {
          this.organizations = res.data
        })
    },
    getResult() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?region_id=${this.regions_id}&district_id=${this.districts_id}&&test_id=${this.tests_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        }
      )
        .then(res=>{
          this.statsExel=[]
          this.test_result=res.data
          this.test_result.results.forEach((el,i)=>{
            this.statsExel.push({
              "ID":el.id,
              "Ф И О":el.full_name,
              "Туғилган санаси":el.date_of_birth,
              "Телефон":el.phone_number,
              "Ҳудуд":el.region_name,
              "Туман":el.district_name,
              "Ташкилот":el.school_name,
            })
          })
        })
    }
  },
  mounted() {

    axios.get(`https://api-kasb.maktab.uz/api/v1/integration/organizations/?region_id=${this.$route.params.reg_id}&district_id=${this.$route.params.des_id}`)
      .then(res => {
        this.organizations = res.data;

        // Array to store all axios requests
        const requests = res.data.result.map(el =>
          axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?school_id=${el.organizationid}`, {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
              'Accept-Language': 'uz'
            }
          })
        );

        // Execute all axios requests concurrently
        Promise.all(requests)
          .then(responses => {
            responses.forEach((ress, index) => {
              // Update the total for each organization
              this.$set(this.organizations.result[index], 'total', ress.data.total);
            });
            // Store organizations data in a separate variable if needed
            this.main_or = this.organizations.result;
            this.loadings=false
          })
          .catch(error => {
            console.error('Error fetching questionnaire answers:', error);
          });
      })
      .catch(error => {
        console.error('Error fetching organizations:', error);
      });

    axios.get(`https://api-kasb.maktab.uz/api/v1/integration/districts/?region_id=${this.$route.params.reg_id}`)
      .then(res => {
        this.districts = res.data
      })
    axios.get(`${baseUrl}tests/`)
      .then(res=>{
        this.tests=res.data
      })

    axios.get('https://api-kasb.maktab.uz/api/v1/integration/regions/')
      .then(res=>{
        this.regions=res.data
      })
    axios.get('https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?test_id=16&&page=2', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Accept-Language':'uz'
        }
      }
    )
      .then(res=>{
        this.test_result=res.data
        this.test_result.results.forEach((el,i)=>{
          this.statsExel.push({
            "ID":el.id,
            "Ф И О":el.full_name,
            "Туғилган санаси":el.date_of_birth,
            "Телефон":el.phone_number,
            "Ҳудуд":el.region_name,
            "Туман":el.district_name,
            "Ташкилот":el.school_name
          })
        })
      })
  }
